import { connect } from 'react-redux'
import Journey from '../components/Journey'
import { showJourney } from '../actions'

const mapDispatchToProps = (dispatch) => {
  return {
    openJourney: () => {
      const selected = 0; // HACK: hard-coded for now
      dispatch(showJourney(true, selected));
      const state = window.store.getState(); // NOTE: Is this the right way?
      window.map.addLayer({
        id:'route',
        source:{
          type:'geojson',
          data:{
            type:'LineString',
            coordinates: state.journey.routes[selected].coordinates
          }
        },
        type:'line',
        paint:{
          "line-width":8,
          "line-color":"#007cbf"
        },
        layout:{
          "line-join":'round',
          "line-cap":'round'
        }
      });
    },
    closeJourney: () => {
      dispatch(showJourney(false));
      window.map.removeLayer('route');
      window.map.removeSource('route');
    },
    flyTo: (e, item) => {
      console.log(item.title);
      window.map.easeTo({
        center: item.loc
      });
    },
  }
}

const mapStateToProps = (state) => {
  return {
    on: state.journey.on,
    route: state.journey.routes[state.journey.selected] || {},
    layout: state.layout,
  }
}

const JourneyFrame = connect(
                             mapStateToProps,
                             mapDispatchToProps
                             )(Journey)

export default JourneyFrame;



