const journey = (state = {on:false, routes:[], selected:0}, action) => {
  switch (action.type) {
    case 'SHOW_JOURNEY':
      return Object.assign({}, state, {on:action.on, selected:action.selected})

    case 'SET_ROUTES':
      return Object.assign({}, state, {routes:action.routes})

    default:
      return state
  }
}

export default journey



